// React
import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
// Overlord
import USER from '../../shared/library/local/user';
import api from '../../shared/library/server/api';
// Assets
import './e-panel.css'
import linkIcon from '../../shared/assets/icons/link.svg';
import userIcon from '../../shared/assets/icons/userImage.svg';
import githubIcon from '../../shared/assets/icons/github.svg';
import settingsIcon from '../../shared/assets/icons/settings.svg';


const PanelView = () => {
  const [ currentView, setView ] = useState<string>('PanelUsers');
  const [ permissions, setPermissions ] = useState<number>(-1);
  const [ panel, setPanel ] = useState<any>({
    id: useParams<any>().panelID
  });
  const user = USER();

  useEffect(() => {
    api(
      `panel/verify/user/${panel.id}/${user.UUID}`,
      (resp:any) => window.location.href = '/e-panel',
      (resp:any) => {
        setPermissions(resp.permissions);
      }
    )
    api(
      `panel/view/${panel.id}`,
      (resp:any) => window.location.href = '/e-panel',
      (resp:any) => {
        setPanel(resp);
      }
    )
  }, [panel.id, user.UUID]);

  if(permissions < 0) return <></>
  return <div id="Panel-Body">
    <div id="Panel-Menu">
      <div id="Panel-Menu-Icons">
        <div className="Panel-Menu-Icon" onClick={() => setView("UserProfile")}>
          <img src={userIcon} alt="profile"/>
        </div>
        <div className="Panel-Menu-Icon" onClick={() => setView("GitHub")}>
          <img src={githubIcon} alt="github"/>
        </div>
        <div className="Panel-Menu-Icon" onClick={() => setView("PanelSettings")}>
          <img src={settingsIcon} alt="edit panel"/>
        </div>
      </div>
      <div id="Panel-Menu-Options">
        <div className="Panel-Menu-Header">Panel</div>
        <div className="Panel-Menu-Option" onClick={() => setView("Analytics")}>Analytics</div>
        <div className="Panel-Menu-Option" onClick={() => setView("PanelUsers")}>Manage Users</div>

        <div className="Panel-Menu-Header">Server</div>
        <div className="Panel-Menu-Option">Control</div>
        <div className="Panel-Menu-Option">Settings</div>

        <div className="Panel-Menu-Header">Clients</div>
        <div className="Panel-Menu-Option">Content</div>
        <div className="Panel-Menu-Option">Settings</div>
      </div>
    </div>
    <div id="Panel-Content">
      { currentView === 'GitHub' ? <GitHub panel={panel}/> : <></> }
      { currentView === 'PanelUsers' ? <PanelUsers panel={panel}/> : <></> }
    </div>
  </div>
}


const GitHub = (props:any) => {
  return <>
    <a href='https://github.com/EasterCompany/EasterCompany'>
      <h2 className="Panel-Option-Header">GitHub <img src={linkIcon} alt="view repository"/></h2>
    </a>
    <div style={{ display: 'flex' }}>
      <div id="Panel-Content-Left">
        <div className="text-banner">
          <b>Create:</b><p>{props.panel.createCMD}</p>
        </div>
        <div className="text-banner">
          <b>Install:</b><p>{props.panel.installCMD}</p>
        </div>
        <div className="text-banner">
          <b>Run:&nbsp;&nbsp;&nbsp;&nbsp;</b><p>{props.panel.runCMD}</p>
        </div>
      </div>
      <div id="Panel-Content-Right" className="markdown-container" style={{flexDirection: 'column'}}>
        <ReactMarkdown children={props.panel.description}/>
      </div>
    </div>
  </>
}


const PanelUsers = (props:any) => {
  const [ users, setUsers ] = useState<any>({});
  const apiPath = `panel/view/${props.panel.id}/users`

  useEffect(()=>{
    api(
      apiPath,
      (resp:any) => null,
      (resp:any) => setUsers(resp)
    );
  }, [apiPath])

  return <>
    <h2 className="Panel-Option-Header">Panel Users</h2>
    <div>
      {Object.keys(users).map((user:any) => <div className="panel-users-user grow">
        <img src={userIcon} alt="profile"/>
        <p>{users[user].email}</p>
        <p style={{ marginLeft: 'auto', textAlign: 'right' }}>(<b>{users[user].permissions}</b>)</p>
      </div>)}
    </div>
  </>
}


export default PanelView
